.primary-color {
  color: #295ee6;
}
* {
  box-sizing: border-box;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  font-family: 'Open Sans';
  font-weight: 300;
}
.bold {
  font-weight: 500;
}
.btn {
  border: none;
  border-radius: 3px;
  color: #fff;
  font-family: 'Open Sans';
  font-weight: 300;
  height: 60px;
  font-size: 1rem;
}
.btn--light {
  color: #295ee6;
  font-weight: 500;
}
.btn--primary {
  background: #295ee6;
  color: #fff;
}
.btn--large {
  width: 100%;
}
.logo__wheel {
  animation: rotateLogo 1s ease-out;
}
.logo__text {
  color: #fff;
  font-size: 1.375rem;
  font-weight: 700;
  margin-top: 16px;
}
@-moz-keyframes rotateLogo {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotateLogo {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-o-keyframes rotateLogo {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotateLogo {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.Login {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(180deg, rgba(41,94,230,0.5), rgba(41,94,230,0.8), #295ee6, #295ee6, #295ee6);
  z-index: 100;
}
.Login__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
.Login__logo {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Login__facebook {
  background: #3c5b97;
  width: 100%;
}
.Login__google {
  background: #4587f0;
  width: 100%;
  margin-top: 16px;
}
.Login__buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
}
.Login__demi {
  width: calc(50% - 8px);
  background: #fff;
}
.Login__text {
  color: #fff;
  text-align: center;
  font-size: 0.875rem;
  height: 10%;
  margin-top: 32px;
  line-height: 1.1875rem;
}
.Modal {
  display: none;
  position: fixed;
  z-index: 200;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
  animation: animatetop 0.4s;
}
.Modal__content {
  margin: 0 auto;
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Modal__title {
  font-size: 1.5rem;
  color: #5f5f5f;
  line-height: 2.0625rem;
}
.Modal__subtitle {
  font-size: 1.125rem;
  color: rgba(95,95,95,0.5);
  line-height: 1.5625rem;
}
.Modal__form {
  width: 100%;
  margin-top: 60px;
}
.Modal__btn {
  margin-top: 28px;
  margin-bottom: 68px;
}
.Modal__text {
  text-align: center;
  font-size: 0.875rem;
  line-height: 1.1875rem;
}
.close {
  position: absolute;
  top: 16px;
  right: 16px;
  color: #aaa;
  font-size: 1.75rem;
  font-weight: 500;
}
.close:hover,
.close:focus {
  color: #000;
text-decoration
  cursor: pointer;
}
@-moz-keyframes animatetop {
  from {
    top: -100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@-webkit-keyframes animatetop {
  from {
    top: -100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@-o-keyframes animatetop {
  from {
    top: -100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@keyframes animatetop {
  from {
    top: -100%;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
.input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 1rem;
  padding: 14px;
  margin-bottom: 32px;
  border-bottom: 1px solid rgba(95,95,95,0.5);
}
